import { ProjectData, projectsService } from './../shared/projects.service';
import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ref, uploadBytes } from 'firebase/storage';
import { storage } from '../shared/storage';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-adm-project',
  templateUrl: './adm-project.component.html',
  styleUrls: ['./adm-project.component.scss'],
})
export class AdmProjectComponent implements OnInit, OnDestroy {
  projects: ProjectData;
  projectKeys: string[] = [];
  loading: boolean;
  deleteLoading: boolean;
  private subscription: Subscription;

  constructor(
    private http: HttpClient,
    private projectService: projectsService
  ) {}
  storage = storage;

  image: any;
  ngOnInit(): void {
    this.projectService.startFetchingProjects();

    this.subscription = this.projectService.projectsData$.subscribe(
      (projects) => {
        this.projects = projects;
        if (this.projects) {
          this.projectKeys = Object.keys(this.projects);
        }
      }
    );
  }
  setProjectInfo(projectsData: ProjectData) {

    console.log(projectsData);
    if (projectsData) {
      this.projects = projectsData;
      this.projectKeys = Object.keys(projectsData);
    } else {
      this.projectKeys = [];
    }
  }

  processImage(image: any) {
    if (image?.files?.length) {

      this.image = image.files[0];
    }
  }
 

  onSubmit(form: NgForm) {
    if (!this.loading) {
      this.loading = true;
      this.uploadImage(form.form.value.title);
      console.log(form.form.value);

      this.http
        .post('https://healthcontrol-76123.firebaseio.com/projects.json', {
          title: form.form.value.title,
          text1: form.form.value?.text1,
          text2: form.form.value?.text2,
        })
        .subscribe((res) => {
          console.log(res);
          this.loading = false;
          form.reset();
          this.projectService.startFetchingProjects();
        });
    }
  }
  uploadImage(title: string) {
    if (typeof localStorage !== 'undefined') { 
      const imageRef = ref(this.storage, `projects/${title}`);

      uploadBytes(imageRef, this.image).then((res) => {
        console.log(res);
      });
    }
   
  }

  onDelete(key: string) {
    this.deleteLoading = true;
    this.http
      .delete(`https://healthcontrol-76123.firebaseio.com/projects/${key}.json`)
      .subscribe(
        (response) => {
          console.log(response);
          console.log('Item deleted successfully');
          this.projectService.startFetchingProjects();
          this.deleteLoading = false;
        },
        (error) => {
          console.log(error);
          console.log('Error deleting item:', error);
        }
      );
  }

  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
